<template>
  <div>
    <vu-modal
      show-cancel-icon
      :title="NLS.ExplainModerationDecision"
      :show="showModal"
      @close="toggleReasonModal(false)"
      class="report-abuse-close-reason"
    >
      <template #modal-body class="close-modal-body">
        <vu-checkbox
          :label="NLS.ClosureReasonLabel1"
          type="radio"
          :options="options1"
          v-model="closureReason"
        >
        </vu-checkbox>
        <vu-checkbox
          :label="NLS.ClosureReasonLabel2"
          type="radio"
          :options="options2"
          v-model="closureReason"
        >
        </vu-checkbox>
        <vu-checkbox
          :label="NLS.ClosureReasonLabel3"
          type="radio"
          :options="options3"
          v-model="closureReason"
        >
        </vu-checkbox>
        <vu-checkbox type="radio" :options="options4" v-model="closureReason">
        </vu-checkbox>
      </template>
      <template #modal-footer>
        <vu-btn color="primary" @click="closeReportReason">
          {{ NLS.Send }}
        </vu-btn>
        <vu-btn @click="toggleReasonModal(false)"> {{ NLS.Cancel }}</vu-btn>
      </template>
    </vu-modal>
    <vu-message
    :show.sync="showMessage"
    class="moderator-message"
    icon="true"
    color="error"
    :timeout="3000"
  >
    {{ NLS.PleaseSelectModerationDecision }}
  </vu-message>
  </div>
 
</template>
<script>
export default {
  name: "SwymClosureReasonModal",
  props: {
    NLS: {
      type: Object,
      default: () => ({}),
    },
    closureType: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      showModal: false,
      model: "",
      closureReason: "",
      showMessage: false,
      options1: [
        {
          value: "INAPPROPRIATE_OFFENSIVE",
          label: this.NLS.InappropriateOffensive,
        },
      ],
      options2: [
        {
          value: "ILLEGAL_CRIMINAL",
          label: this.NLS.IllegalCriminal,
        },
        {
          value: "ILLEGAL_USER_PRIVACY",
          label: this.NLS.IllegalUserPrivacy,
        },
        {
          value: "ILLEGAL_HARASSMENT",
          label: this.NLS.IllegalHarassment,
        },
      ],
      options3: [
        {
          value: "INAPPROPRIATE_ADVERTISEMENT",
          label: this.NLS.InappropriateAdvertisement,
        },
        {
          value: "INAPPROPRIATE_PERSONAL_OPINION",
          label: this.NLS.InappropriatePersonalOpinion,
        },
        {
          value: "INAPPROPRIATE_SEXUAL_LINKED",
          label: this.NLS.InappropriateSexualLinked,
        },
      ],
      options4: [
        {
          value: "FAKE_IDENTITY",
          label: this.NLS.FakeIdentity,
        },
        {
          value: "THIRD_PARTY_VIOLATION",
          label: this.NLS.ThirdPartyViolation,
        },
      ],
    };
  },
  methods: {
    toggleReasonModal(modalVisibility) {
      this.showModal = modalVisibility;
      this.closureReason = null;
    },
    closeReportReason() {
      if (!this.closureReason) {
        this.showMessage = true;
        this.showModal = true;
      } else {
        this.$emit("assignToAdmin", {
          updatedStatus: "CLOSED",
          closureReason: this.closureReason,
          closureType: this.closureType,
        });
        this.showModal = false;
      
      }
      this.closureReason = null;
      
    },
  },
};
</script>
<style lang="scss">
.report-abuse-close-reason {
  position: fixed;
  z-index: 2000 !important;
}
.moderator-message {
  color: black;
  top: 0;
  position: fixed;
  left: 45%;
  z-index: 3000;
}
</style>
