<template>
  <div>
    <vu-modal
      show-cancel-icon
      :title="NLS.CloseReport"
      :show="showClose"
      @close="toggleCloseModal(false)"
      class="report-abuse-close-modal"
    >
      <template #modal-body class="close-modal-body">
        <vu-checkbox
          :label="NLS.SelectClosureType"
          required
          type="radio"
          :options="options"
          v-model="model"
        >
        </vu-checkbox>
      </template>
      <template #modal-footer>
        <vu-btn color="primary" @click="closeReport">
          {{ NLS.Confirm }}
        </vu-btn>
        <vu-btn @click="toggleCloseModal(false)"> {{ NLS.Cancel }}</vu-btn>
      </template>
    </vu-modal>
    <SwymClosureReasonModal
      ref="reasonRef"
      :closureType="closureType"
      @assignToAdmin="assignToAdmin"
      :NLS="NLS"
    />
    <vu-message
      :show.sync="showMessage"
      class="moderator-message"
      icon="true"
      color="error" 
      :timeout="3000"
    >
     {{ NLS.PleaseSelectClosureType }}
    </vu-message>
  </div>
</template>
<script>
import SwymClosureReasonModal from "./SwymClosureReasonModal.vue";

export default {
  name: "SwymReportAbuseCloseModal",
  components: {
    SwymClosureReasonModal,
  },
  props: {
    NLS: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      showClose: false,
      closureType: [],
      totalClosureType: [],
      model: '',
      showMessage: false,

      options: [
        {
          value: "REJECTED",
          label: this.NLS.RefusedCloseType,
        },
        {
          value: "CONTENT_SELF_UPDATED",
          label: this.NLS.AuthorEditedTheContent,
        },
        {
          value: "CONTENT_SELF_DELETED",
          label: this.NLS.AuthorDeletedTheContent,
        },
        {
          value: "CONTENT_UPDATED",
          label: this.NLS.ModeratorEditedTheContent,
        },
        {
          value: "CONTENT_DELETED",
          label: this.NLS.ModeratorDeletedTheContent,
        },
      ],
    };
  },

  methods: {
    toggleCloseModal(modalVisibility) {
      this.showClose = modalVisibility;
      this.model = null;
    },
    closeReport() {
      if (this.model === "REJECTED") {
        this.$emit("assignToAdmin", { updatedStatus: "REJECTED" });
        this.showClose = false;
      } 
      else if (
        this.model === "CONTENT_UPDATED" ||
        this.model === "CONTENT_DELETED") 
      {
        this.$refs.reasonRef.toggleReasonModal(true);
        this.closureType = [this.model];
        this.showClose = false;
      }  
      else if (!this.model) {
        this.showMessage = true;
        this.showClose = true;
      } 
      else {
        this.totalClosureType = [this.model];
        this.$emit("assignToAdmin", {
          updatedStatus: "CLOSED",
          closureType: this.totalClosureType,
        });
        this.showClose = false;
      }
      this.model = null;
    },

    assignToAdmin(data) {
      this.$emit("assignToAdmin", {
        updatedStatus: data.updatedStatus,
        closureReason: data.closureReason,
        closureType: data.closureType,
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.close-modal-body {
  text-align: center;
}
.moderator-message {
  color: black;
  top: 0;
  position: fixed;
  left: 45%;
  z-index: 2500;
}
</style>
<style>
.report-abuse-close-modal {
  position: fixed;
  z-index: 2000 !important;
}
</style>
