<template>
  <div id="swym-report-abuse-data">
    <table class="report-abuse-data-table">
      <thead>
        <tr>
          <th>{{ NLS.Reporter }}</th>
          <th>{{ NLS.Date }}</th>
          <th>{{ NLS.Reason }}</th>
          <th>{{ NLS.Comment }}</th>
          <th>{{ NLS.Snapshot }}</th>
        </tr>
      </thead>
      <tbody>
        <template v-for="data in dataFromModeration" :key="data.reference">
          <tr>
            <td>{{ `${data.user.first_name} ${data.user.last_name}` }}</td>
            <td>
              {{
              new Date(data.modification_date).toLocaleString()
              }}
            </td>
            <td>{{ mapReportAbuseReason(data.reason) }}</td>
            <td class="report-body" >
              <span v-tooltip.body="data.body">
                {{ data.body }}
              </span>
            </td>
            <td>
              <vu-icon-btn
                icon="window"
                @click="getSnapshotData(data.reference)"
              />
            </td>
          </tr>
        </template>
      </tbody>
    </table>
  </div>
</template>

<script>
import reportAbuse from '../../mixins/reportAbuse';
export default {
  name: "SwymReportAbuseData",
  mixins:[reportAbuse],
  props: {
    dataFromModeration: {
      value: Array,
      default: ()=>([]),
    },

    NLS: {
      type: Object,
      default: () => ({}),
    },
  },

  methods: {
    getSnapshotData(reportReferenceId) {
      this.$emit("getSnapshotData", reportReferenceId);
    },
  },
};
</script>
