<template>
    <vu-modal
        :show="show"
        :title="title"
        @close="onCancel"
    >
        <template #modal-body>
            <label>{{ $i18n('messageInfoDefaultWiki') }}</label>
            <TreeView ref="treeView"
                :tree="wikiPagesToDisplay"
                :multipleActive="true"
                :activatable="true"
                :isVisible="true"
                :openAll="true"
                :selectedByDefault="pagesSelectedByDefault"
            />
        </template>
        <template #modal-footer>
            <div class="modal-footer_container">
                <vu-btn 
                class="select-all_button" 
                @click="selectAllPages"
                >
                    {{ $i18n('selectAll') }}
                </vu-btn>
                <vu-btn 
                    color="primary" 
                    @click="onUpdate"
                    :loading="updating"
                > 
                    {{ $i18n('update') }}
                </vu-btn>
                <vu-btn 
                    @click="onCancel"
                >
                    {{ $i18n('cancel') }}
                </vu-btn>
            </div>
        </template>
    </vu-modal>

</template>
<script>
export default {
    name: 'SwymDefaultContentWikiTreeUpdateModal',
    props: {
        title: {
            type: String,
            default: '',
        },
        show:{
            type: Boolean, 
            default: false
        },
        wikiPagesToDisplay: {
            type: Array,
            default: []
        },
        pagesSelectedByDefault: {
            type: Array,
            default: []
        }
    },
    emits: [
        'update', 'cancel', 'close'
    ],
    data(){
        return {
            updating: false,
        }
    },
    mounted(){
        this.$emit('requestFullScreen');
    },
    methods:{
        onCancel() {
            this.$emit('leaveFullScreen')
            this.$emit('cancel')
        },
        onUpdate(){
            this.$emit('leaveFullScreen')
            const pageSelected = this.$refs.treeView.getActiveSelection();
            this.$emit('update', pageSelected);
        },
        isUpdating: function() {
            this.updating = true
        },
        updateEnding: function(options) {
            this.$message({text: options.text, color: options.color, timeout: options.timeout})
            this.updating = false
        },
        selectAllPages() {
            this.$refs.treeView.selectAll();
        }
    }
}
</script>

<style lang="scss">
.modal-content{
   top: -10px;
}
</style>

<style lang="scss" scoped>
.modal-footer_container{
    display: flex;
    justify-content: flex-end;

    .select-all_button {
        margin-right: auto;
    }
}
</style>
