<template>
    <vu-modal
        :show="show"
        :title="title"
        @close="$emit('close')"
    >
        <template #modal-body>
            <!-- Source -->
            <h5>
                <vu-icon icon="flow-branch-downward"/>
                {{ $i18n('source') }}
            </h5>
            <div class="section section-source">
                <span>
                    {{ $i18n('capturedFrom') }}
                    <span class="btn-link" @click="$emit('contentNameClick')">{{ contentName }}</span>
                </span>
                <span>
                    {{ $i18n('community') }}
                    <span class="btn-link" @click="$emit('communityNameClick')">{{ communityName }}</span>
                </span>
                <span class="modified-date">
                    {{ $i18n('modified') }}
                    <span>{{ modifiedDate }}</span>
                </span>
            </div>

            <!-- Capture -->
            <div class="section section-capture">
                <h5>
                    <vu-icon icon="update"/>
                    {{ $i18n('captureDate') }}
                </h5>
                <span>{{ captureDate }}</span>
                <vu-btn 
                    color="primary" 
                    @click="$emit('update')"
                    :loading="updating"
                >
                    {{ $i18n('update') }}
                </vu-btn>
            </div>    
        </template>

        <template #modal-footer>
            <vu-btn
                v-if="!hideDelete"
                @click="$emit('delete')"
                color="error"
            >
                {{ $i18n('delete') }}
            </vu-btn>
            <vu-btn @click="$emit('cancel')">
                {{ $i18n('cancel') }}
            </vu-btn>
        </template>
    </vu-modal>
  </template>
  
  <script>
  export default {
    name: 'SwymUpdateContentModal',
    props: {
        show: Boolean,
        title: String,
        contentName: String,
        communityName: String,
        modifiedDate: String,
        captureDate: String,
        hideDelete: {
            type: Boolean,
            default: false
        },
    },
    emits: [
        'update', 'delete', 'cancel', // Modal events
        'contentNameClick', 'communityNameClick', // Link events
    ],
    data() {
        return {
            updating: false,
        }
    },
    computed: {},
    methods: {
        isUpdating: function() {
            this.updating = true
        },
        updateEnding: function(options) {
            this.$message({text: options.text, color: options.color, timeout: options.timeout})
            this.updating = false
        }
    }
  }
  </script>
  
