<template>
  <div id="swym-report-abuse-management">
    <div class="report-abuse-management-header">
      <span class="info-management">{{
        NLS.AccessRightsToModerateCommunity
      }}</span>
      <span>
        <vu-btn
          :color="isCurrentUserSu ? 'primary' : 'warning'"
          class="moderation-button"
          @click="toggleModerationButton"
        >
          {{
            `${
              isCurrentUserSu ? NLS.EnterModerationMode : NLS.ExitmoderationMode
            }`
          }}
        </vu-btn>
      </span>
      <span>
        <vu-btn class="export-legacy-button" @click="getLegacyReport">
          {{ NLS.ExportLegacyReport || "Export legacy reports" }}
        </vu-btn>
      </span>
    </div>

    <div class="report-abuse-table">
      <table>
        <thead class="table-header">
          <tr ref="focusEditor">
            <th class="reference-id" id="report-abuse-table-header">
              <vu-popover side="bottom" :persistant="false">
                {{ NLS.ReportID }}
                <vu-icon-btn ref="dropdown-btn" icon="chevron-down" />
                <template slot="body">
                  <input
                    @keyup.enter="updateReferenceID"
                    v-model="inputReferenceID"
                    ref="inputField"
                    class="reference-input"
                  />
                  <vu-icon
                    v-if="inputReferenceID"
                    @click="clearInput"
                    icon="clear"
                    class="clear-icon"
                  />
                </template>
              </vu-popover>
            </th>
            <th>{{ NLS.Content }}</th>
            <th id="report-abuse-table-header">
              <vu-popover side="bottom" :persistant="false">
                {{ NLS.Author }}
                <vu-icon-btn ref="dropdown-btn" icon="chevron-down" />
                <template slot="body">
                  <div>
                    <input
                      @keyup="getUserData"
                      v-model="currentAuthor"
                      ref="inputField"
                      class="reference-input"
                    />
                    <vu-icon
                      v-if="currentAuthor"
                      @click="clearAuthorInput"
                      icon="clear"
                      class="clear-icon"
                    />
                  </div>
                  <template>
                    <vu-spinner v-if="isLoading" />
                    <span class="report-abuse-user-list" v-if="!showDropdown">
                      <ul v-for="data in authorList">
                        <li
                          @click="
                            getFilterData({
                              authorLogin: data.value,
                              authorLabel: data.label,
                              currentStatus: currentStatus,
                              currentModerator: currentModerator,
                            })
                          "
                        >
                          {{ data.label }}
                        </li>
                      </ul>
                    </span>
                    <span class="report-abuse-user-list" v-show="showDropdown">
                      <ul v-for="data in userSearch">
                        <li
                          @click="
                            getFilterData({
                              authorLogin: data.login,
                              authorLabel: data.nickname,
                              currentStatus: currentStatus,
                              currentModerator: currentModerator,
                            })
                          "
                        >
                          {{ data.nickname }}
                        </li>
                      </ul>
                    </span>
                    <span class="report-abuse-user-list" v-show="showDropdown">
                      {{ noResultMessage }}
                    </span>
                  </template>
                </template>
              </vu-popover>
            </th>
            <th>{{ NLS.Reason }}</th>
            <th>{{ NLS.NumberOfReports }}</th>
            <th>{{ NLS.Date }}</th>
            <th>{{ NLS.RelatedContent }}</th>
            <th>{{ NLS.Community }}</th>
            <th id="report-abuse-table-header" class="status-list">
              <vu-popover side="bottom" :persistant="false">
                {{ NLS.Status }}
                <vu-icon-btn ref="dropdown-btn" icon="chevron-down" />
                <template slot="body">
                  <vu-select
                    ref="focusInput"
                    required
                    autocomplete
                    :options="status"
                    v-model="currentStatus"
                    @input="
                      getFilterData({
                        authorLogin: authorLabel,
                        authorLabel: currentAuthor,
                        currentStatus: currentStatus,
                        currentModerator: currentModerator,
                      })
                    "
                    class="popover-author status-list"
                  />
                </template>
              </vu-popover>
            </th>
            <th id="report-abuse-table-header" class="moderator-list">
              <vu-popover side="bottom" :persistant="false">
                {{ NLS.Moderator }}
                <vu-icon-btn ref="dropdown-btn" icon="chevron-down" />
                <template slot="body">
                  <vu-select
                    ref="focusInput"
                    required
                    autocomplete
                    :options="moderatorList"
                    v-model="currentModerator"
                    @input="
                      getFilterData({
                        authorLogin: authorLabel,
                        authorLabel: currentAuthor,
                        currentStatus: currentStatus,
                        currentModerator: currentModerator,
                      })
                    "
                    class="popover-author"
                  />
                </template>
              </vu-popover>
            </th>
          </tr>
        </thead>
        <tbody v-if="reportAbuseManagement.length === 0">
          <tr>
            <td>{{ NLS.NoReportAbuseData }}</td>
          </tr>
        </tbody>
        <tbody v-else>
          <template v-for="data in reportAbuseManagement" :key="data.reference">
            <tr @click="openModal(data.uri, data, data.reference)">
              <td>{{ data.reference }}</td>
              <td>
                <div class="content-data">
                  <span class="content-icon">
                    <vu-icon
                      :icon="mapIconData(data.content.type)"
                      :class="`icon-${mapIconData(data.content.type)}`"
                    />
                  </span>
                  <span>
                    <a
                      @click="
                        navigationReportAbuse({
                          urlChain: data.content.nav_uri_chain,
                          contentId: data.subject.subject6wuri,
                          contentType: data.subject.type.toLowerCase(),
                          communityId: data.context.subject6wuri,
                          commentUri: data.content.subject6wuri,
                        })
                      "
                      @click.stop
                      v-tooltip.body="data.content.title"
                    >
                      {{ mapContentType(data.content.type) }}
                    </a>
                  </span>
                </div>
              </td>
              <td class="author-name">
                {{
                  `${data.content.author.first_name} ${data.content.author.last_name}`
                }}
              </td>
              <td>{{ mapReportAbuseReason(data.latest_report.reason) }}</td>
              <td>{{ data.nb_reports }}</td>
              <td>
                {{
                  new Date(
                    data.latest_report.modification_date
                  ).toLocaleString()
                }}
              </td>
              <td>
                <div class="content-title">
                  <span class="content-icon">
                    <vu-icon
                      :icon="mapIconData(data.subject.type)"
                      :class="`icon-${mapIconData(data.subject.type)}`"
                    />
                  </span>
                  <span>
                    {{ data.subject.title }}
                  </span>
                </div>
              </td>
              <td>
                <span>{{ data.context.title }}</span>
              </td>
              <td>
                <SwymReportAbuseStatus :statusData="data.status" :NLS="NLS" />
              </td>
              <td class="moderator-name">
                {{
                  data.moderator
                    ? `${data.moderator.first_name} ${data.moderator.last_name}`
                    : ""
                }}
              </td>
            </tr>
          </template>
        </tbody>
      </table>

      <SwymReportAbuseDataModal
        ref="modalRef"
        :NLS="NLS"
        @assignToAdmin="assignToAdmin"
        :referenceIdModal="listItems"
        :dataForEachContent="reportAbuseModeration"
        :platformAdminList="platformAdminList"
        :currentUserTenantSu="currentUser"
        :currentUserLogin="currentUserLogin"
        @getModerationModeOn="getModerationModeOn"
        @getModerationModeExit="getModerationModeExit"
        :moderationHistoryDataModal="moderationHistory"
        @getSnapshotData="getSnapshotData"
        @navigationReportAbuse="navigationReportAbuse"
      />
    </div>
    <div class="pagination-modal">
      <vu-btn
        class="small-icon-btn"
        @click="loadMore"
        :disabled="isLoadMoreButtonDisable"
      >
        {{ NLS.More }}
        <span class="fonticon fonticon-chevron-down load-more"></span>
      </vu-btn>
    </div>
  </div>
</template>

<script>
import SwymReportAbuseDataModal from "./SwymReportAbuseDataModal.vue";
import SwymReportAbuseStatus from "./SwymReportAbuseStatus.vue";
import debounce from "../../utils/debounce";

import reportAbuse from "../../mixins/reportAbuse";

export default {
  name: "SwymReportAbuseManagement",
  components: {
    SwymReportAbuseDataModal,
    SwymReportAbuseStatus,
  },
  mixins: [reportAbuse],
  props: {
    NLS: {
      type: Object,
      default: () => ({}),
    },
    reportAbuseManagement: {
      type: Array,
      default: () => [],
      required: false,
    },
    currentUser: {
      type: String,
      default: "",
    },
    currentUserLogin: {
      type: String,
      default: "",
    },
    userSearch: {
      type: Array,
      default: () => [],
      required: false,
    },

    reportAbuseModeration: {
      type: Array,
      default: () => [],
      required: false,
    },

    platformAdminList: {
      type: Array,
      default: () => [],
    },
    moderationHistory: {
      type: String,
      default: "",
      required: false,
    },
    isLoading: {
      type: Boolean,
      default: false,
    },
    noResultMessage: {
      type: String,
      default: "",
    },
  },

  data() {
    return {
      showDataModal: false,
      listItems: null,
      refId: "",
      currentAuthor: "",
      currentPage: 0,
      currentModerator: "",
      inputReferenceID: "",
      currentStatus: "",
      authorLabel: "",
      showDropdown: false,
      status: [
        { value: "OPENED", label: this.NLS.Opened },
        { value: "ASSIGNED", label: this.NLS.Assigned },
        { value: "UNDER_REVIEW", label: this.NLS.UnderReview },
        { value: "CLOSED", label: this.NLS.Closed },
        { value: "REJECTED", label: this.NLS.Refused },
      ],
      reportLength: 0,
      showButton: true,
    };
  },

  methods: {
    getLegacyReport(){
      this.$emit("getLegacyReport")
    },
    navigationReportAbuse(data) {
      this.$emit("navigationReportAbuse", data);
    },
    getUserData: debounce(function (e) {
      if (e.target.value !== "") {
        this.$emit("getUserData", e.target.value);
        this.showDropdown = true;
      } else {
        this.showDropdown = false;
      }
    }, 500),

    loadMore() {
      if (this.currentAuthor !== "" || this.currentModerator !== "") {
        this.$emit("getFilterData", {
          author: this.authorLabel,
          moderator: this.currentModerator,
          page: ++this.currentPage,
        });
      } else if (this.currentStatus !== "") {
        this.$emit("getFilterData", {
          author: this.authorLabel,
          moderator: this.currentModerator,
          status: this.currentStatus,
          page: ++this.currentPage,
        });
      } else {
        this.$emit("getNextPage", ++this.currentPage);
      }
    },
    openModal(moderationURI, referenceData, referenceId) {
      referenceData.moderator ? referenceData.moderator : "";
      this.listItems = referenceData;
      this.refId = moderationURI;
      this.$emit("getReportsForContent", moderationURI);
      this.$emit("getModerationHistoryData", referenceId);
      this.$refs.modalRef.toggleModal(true);
    },
    assignToAdmin(data) {
      this.$emit("assignToAdmin", {
        login: data.login,
        refId: this.refId,
        updatedStatus: data.updatedStatus,
        closureType: data.closureType,
        closureReason: data.closureReason,
        reference: data.inputReferenceID,
      });
    },
    getSnapshotData(data) {
      this.$emit("getSnapshotData", data);
    },
    getModerationModeOn() {
      this.$emit("getModerationModeOn");
    },
    getModerationModeExit() {
      this.$emit("getModerationModeExit");
    },
    getFilterData(data) {
      this.currentPage = 0;
      if (this.currentStatus === "") {
        this.$emit("getFilterData", {
          author: data.authorLogin,
          moderator: data.currentModerator,
        });
      } else {
        this.$emit("getFilterData", {
          moderator: data.currentModerator,
          status: data.currentStatus,
          author: data.authorLogin,
        });
      }
      this.authorLabel = data.authorLogin;
      this.currentAuthor = data.authorLabel;
      this.$refs.focusEditor.click();
    },
    updateReferenceID() {
      if (this.inputReferenceID === "") {
        this.currentPage = 0;
        this.$emit("getControlCenterData");
      } else {
        this.$emit("getFilterReferanceIDData", {
          reference: this.inputReferenceID,
        });
      }
      this.$refs.focusEditor.click();
    },
    clearInput() {
      this.inputReferenceID = "";
      this.updateReferenceID(this.inputReferenceID);
      this.$refs.inputField.click();
    },
    clearAuthorInput() {
      this.currentAuthor = "";
      this.getFilterData({
        currentAuthor: this.currentAuthor,
        currentStatus: this.currentStatus,
        currentModerator: this.currentModerator,
      });
      this.showDropdown = false;
    },
    toggleModerationButton() {
      if (this.isCurrentUserSu) {
        this.$emit("getModerationModeOn");
      } else {
        this.$emit("getModerationModeExit");
      }
    },
    mapIconData(iconData) {
      let icon;
      if (iconData.includes("POST")) {
        icon = "newspaper";
        return icon;
      } else if (iconData.includes("IDEATION")) {
        icon = "lamp";
        return icon;
      } else if (iconData.includes("QUESTION")) {
        icon = "comment-question";
        return icon;
      } else if (iconData.includes("WIKI_PAGE")) {
        icon = "book-open";
        return icon;
      } else if (iconData.includes("SURVEY")) {
        icon = "clipboard";
        return icon;
      } else if (iconData.includes("MEDIA")) {
        icon = "picture";
        return icon;
      } else if (iconData.includes("CONFERENCE")) {
        icon = "calendar";
        return icon;
      } else if (iconData.includes("WEDO")) {
        icon = "task";
        return icon;
      } else if (iconData.includes("COMMENT")) {
        icon = "comment";
        return icon;
      } else if (iconData.includes("ANSWER")) {
        icon = "comment";
        return icon;
      }
    },
  },

  computed: {
    isLoadMoreButtonDisable() {
      if (
        this.reportAbuseManagement.length === 0 ||
        this.reportAbuseManagement.length < (this.currentPage + 1) * 15
      ) {
        return true;
      }
    },
    isCurrentUserSu() {
      if (this.currentUser === "0") {
        return true;
      } else {
        return false;
      }
    },

    moderatorList() {
      return this.platformAdminList.map((el, index) => {
        return {
          value: el.login,
          label: `${el.first_name} ${el.last_name}`,
          index,
        };
      });
    },
    authorList() {
      const nameSet = new Set();
      return this.reportAbuseManagement
        .map((el, index) => {
          return {
            value: el.content.author.login,
            label: `${el.content.author.first_name} ${el.content.author.last_name}`,
            index,
          };
        })
        .filter((item) => {
          if (nameSet.has(item.value)) {
            return false;
          }
          nameSet.add(item.value);
          return true;
        });
    },
  },
};
</script>
