<template>
  <vu-modal
    show-cancel-data
    :title="NLS.Moderate"
    :show="showModal"
    @close="toggleModeration(false)"
    class="moderation-modal"
  >
    <template #modal-body>
      <div class="moderator-body">
        <p>{{ NLS.ModerationTextP1 }}</p>
        <p>{{ NLS.ModerationTextP2 }}</p>
      </div>
    </template>
    <template #modal-footer>
      <vu-btn color="primary" @click="getModerationModeOn">
        <a
              @click="
                navigationReportAbuse({
                  urlChain: referenceIdModal.content.nav_uri_chain,
                  contentId: referenceIdModal.subject.subject6wuri,
                  contentType: referenceIdModal.subject.type.toLowerCase(),
                  communityId: referenceIdModal.context.subject6wuri,
                  commentUri: referenceIdModal.content.subject6wuri,
                })
              "
               target="_blank"
          class="continue-btn"
            >
            {{ NLS.Continue }}
            </a>
        
      </vu-btn>
      <vu-btn @click="toggleModeration(false)"> {{ NLS.Cancel }}</vu-btn>
    </template>
  </vu-modal>
</template>

<script>
import reportAbuse from "../../mixins/reportAbuse";
export default {
  name: "SwymModerationButtonModal",
  mixins: [reportAbuse],
  props: {
    referenceIdModal: {
      value: Object,
      default: () => ({}),
    },
    currentUserTenantSu: {
      type: String,
      default: "",
    },
    NLS: {
      type: Object,
      default: () => ({}),
    },
    currentUserLogin: {
      type: String,
      default: "",
    },
      },
  data() {
    return {
      showModal: false,
    };
  },
  methods: {
    navigationReportAbuse(data) {
      this.$emit("navigationReportAbuse", data);
    },
    toggleModeration(modalVisibility) {
      this.showModal = modalVisibility;
    },
    getModerationModeOn() {
      if (
        this.currentUserTenantSu === "0"
      ) {
        this.$emit("getModerationModeOn");
        this.$emit("assignToAdmin", {login: this.currentUserLogin, updatedStatus: "UNDER_REVIEW" });
        this.showModal = false;

      } else if (
        this.currentUserTenantSu === "1" 
      ) {
        this.$emit("assignToAdmin", {login: this.currentUserLogin, updatedStatus: "UNDER_REVIEW" });
        this.showModal = false;
      } else {
        this.showModal = false;
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.continue-btn {
  color: white !important;
  text-decoration: none;
}
.moderator-body {
  padding: 50px;
}
</style>
<style>
.moderation-modal {
  position: fixed;
  z-index: 2000 !important;
}
</style>
