<template>
  <div class="moderation-history-modal">
    <p class="history">{{ NLS.History }}</p>
    <div class="moderation-history-data">
      <ul>
      <li v-for="(data, index) in moderationHistoryDataModal" :key="index">
        <div v-if="data.status === 'REJECTED'" class="moderation-history">
          <span>{{ new Date(data.creation_date).toLocaleDateString() }}</span>
          <span class="moderation-time">{{
             new Date(data.creation_date).toLocaleTimeString()
          }}</span>
          <span>
            <div>
              <span class="moderator-name">{{
                `${data.actor.first_name} ${data.actor.last_name} `
              }}</span>
              <span>{{ NLS.RefuseTheReport }}</span>
            </div>
          </span>
        </div>
        <div v-if="data.status === 'CLOSED'" class="moderation-history">
          <span>{{ new Date(data.creation_date).toLocaleDateString() }}</span>
          <span class="moderation-time">{{
             new Date(data.creation_date).toLocaleTimeString()
          }}</span>
          <span>
            <div>
              <span class="moderator-name">{{
                `${data.actor.first_name} ${data.actor.last_name} `
              }}</span>
              <span>{{ NLS.CloseTheReport }}</span>
            </div>
            <div
              v-for="(closureType, index) in data.closure_types"
              :key="index"
            >
              {{ `- ${mapClosureReasonAndType(closureType)}` }}
            </div>
            <div v-if="data.closure_reason">
              {{ `- ${mapClosureReasonAndType(data.closure_reason)}` }}
            </div>
          </span>
        </div>
        <div v-if="data.status === 'UNDER_REVIEW'" class="moderation-history">
          <span>{{ new Date(data.creation_date).toLocaleDateString() }}</span>
          <span class="moderation-time">{{
             new Date(data.creation_date).toLocaleTimeString()
          }}</span>
          <span>
            <span class="moderator-name">{{
              `${data.actor.first_name} ${data.actor.last_name} `
            }}</span>
            <span>{{ NLS.SwitchToUnderReview }}</span>
          </span>
        </div>
        <div
          v-if="data.action === 'MODERATOR_ASSIGNED'"
          class="moderation-history"
        >
          <span>{{ new Date(data.creation_date).toLocaleDateString() }}</span>
          <span class="moderation-time">{{
            new Date(data.creation_date).toLocaleTimeString()
          }}</span>
          <span>
            <span class="moderator-name">{{
              `${data.actor.first_name} ${data.actor.last_name} `
            }}</span>
            {{ NLS.AssignedHistory }}
            <span class="moderator-name">{{
              `${data.moderator.first_name} ${data.moderator.last_name} `
            }}</span>
          </span>
        </div>

        <div
          v-if="data.action === 'CONTENT_UPDATED'"
          class="moderation-history"
        >
          <span>{{ new Date(data.creation_date).toLocaleDateString() }}</span>
          <span class="moderation-time">{{
             new Date(data.creation_date).toLocaleTimeString()
          }}</span>
          <span>
            <span class="moderator-name">{{
              `${data.actor.first_name} ${data.actor.last_name} `
            }}</span>
            {{ NLS.EditedTheContent }}
          </span>
        </div>
        <div
          v-if="data.action === 'CONTENT_DELETED'"
          class="moderation-history"
        >
          <span>{{ new Date(data.creation_date).toLocaleDateString() }}</span>
          <span class="moderation-time">{{
            new Date(data.creation_date).toLocaleTimeString()
          }}</span>
          <span>
            <span class="moderator-name">{{
              `${data.actor.first_name} ${data.actor.last_name} `
            }}</span>
            {{ NLS.DeletedTheContent }}
          </span>
        </div>
      </li>
    </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: "SwymModerationHistory",
  props: {
    moderationHistoryDataModal: {
      value: Array,
      default: () => [],
      required: true,
    },
    NLS: {
      type: Object,
      default: () => ({}),
    },
  },
  methods: {
    mapClosureReasonAndType(reason) {
      let data;
      if (reason === "CONTENT_SELF_UPDATED") {
        data = this.NLS.AuthorEditedTheContent;
        return data;
      } else if (reason === "CONTENT_SELF_DELETED") {
        data = this.NLS.AuthorDeletedTheContent;
        return data;
      } else if (reason === "CONTENT_UPDATED") {
        data = this.NLS.ModeratorEditedTheContent;
        return data;
      } else if (reason === "CONTENT_DELETED") {
        data = this.NLS.ModeratorDeletedTheContent;
        return data;
      } else if (reason === "USER_SUSPEND") {
        data = this.NLS.UserAccountSuspended;
        return data;
      } else if (reason === "USER_BANNED") {
        data = this.NLS.UserAccoundBannedFrom3DSwym;
        return data;
      } else if (reason === "ILLEGAL_CRIMINAL") {
        data = this.NLS.IllegalCriminal;
        return data;
      } else if (reason === "ILLEGAL_USER_PRIVACY") {
        data = this.NLS.IllegalUserPrivacy;
        return data;
      } else if (reason === "ILLEGAL_HARASSMENT") {
        data = this.NLS.IllegalHarassment;
        return data;
      } else if (reason === "INAPPROPRIATE_OFFENSIVE") {
        data = this.NLS.InappropriateOffensive;
        return data;
      } else if (reason === "INAPPROPRIATE_ADVERTISEMENT") {
        data = this.NLS.InappropriateAdvertisement;
        return data;
      } else if (reason === "INAPPROPRIATE_PERSONAL_OPINION") {
        data = this.NLS.InappropriatePersonalOpinion;
        return data;
      } else if (reason === "INAPPROPRIATE_SEXUAL_LINKED") {
        data = this.NLS.InappropriateSexualLinked;
        return data;
      } else if (reason === "FAKE_IDENTITY") {
        data = this.NLS.FakeIdentity;
        return data;
      } else if (reason === "THIRD_PARTY_VIOLATION") {
        data = this.NLS.ThirdPartyViolation;
        return data;
      }
    },
  },
};
</script>
