<template>
  <div>
    <vu-modal
      v-if="referenceIdModal"
      show-cancel-icon
      :title="`${NLS.ReportId}  ${referenceIdModal.reference}`"
      :show="show"
      @close="toggleModal(false)"
      class="report_abuse_modal"
    >
      <template #modal-body>
        <div class="report-abuse" ref="adminChangeRef">
          <div class="content-data report-abuse-data">
            <span class="content-icon">
              <vu-icon
                :icon="mapIconData(referenceIdModal.content.type)"
                :class="`icon-${mapIconData(referenceIdModal.content.type)}`"
              />
            </span>
            <a
              @click="
                navigationReportAbuse({
                  urlChain: referenceIdModal.content.nav_uri_chain,
                  contentId: referenceIdModal.subject.subject6wuri,
                  contentType: referenceIdModal.subject.type.toLowerCase(),
                  communityId: referenceIdModal.context.subject6wuri,
                  commentUri: referenceIdModal.content.subject6wuri,
                })
              "
              @click.stop
              v-tooltip.body="referenceIdModal.content.title"
            >
              {{ mapContentType(referenceIdModal.content.type) }}
            </a>
          </div>
          <div class="report-abuse-data">
            {{
              `${referenceIdModal.content.author.first_name} ${referenceIdModal.content.author.last_name}`
            }}
          </div>
          <div class="content-title report-abuse-data">
            <span class="content-icon">
              <vu-icon
                :icon="mapIconData(referenceIdModal.subject.type)"
                :class="`icon-${mapIconData(referenceIdModal.subject.type)}`"
              />
            </span>
            <span>
          <a
                @click="
                  navigationReportAbuse({
                    urlChain: referenceIdModal.content.nav_uri_chain,
                    contentId: referenceIdModal.subject.subject6wuri,
                    contentType: referenceIdModal.subject.type.toLowerCase(),
                    communityId: referenceIdModal.context.subject6wuri,
                    commentUri: referenceIdModal.content.subject6wuri,
                  })
                "
                @click.stop
              >
                {{ referenceIdModal.subject.title }}
            </a>
            </span>
          </div>
          <div class="community-title report-abuse-data">
           <a
              @click="
                navigationReportAbuse({
                  urlChain: referenceIdModal.content.nav_uri_chain,
                  contentId: null,
                  contentType: referenceIdModal.subject.type.toLowerCase(),
                  communityId: referenceIdModal.context.subject6wuri,
                  commentUri: null,
                })
              "
              @click.stop
            >
              {{ referenceIdModal.context.title }}
            </a>
          </div>
          <div class="modal-status report-abuse-data">
            <SwymReportAbuseStatus
              :statusData="referenceIdModal.status"
              :NLS="NLS"
            />
          </div>
          <div
            class="user-change report-abuse-data"
            v-if="
              referenceIdModal.status === 'UNDER_REVIEW' ||
              referenceIdModal.status === 'ASSIGNED'
            "
          >
            <span class="moderator-name">{{
              `${referenceIdModal.moderator.first_name} ${referenceIdModal.moderator.last_name}`
            }}</span>
            <vu-popover side="bottom" :persistant="false">
              <vu-icon-btn
                ref="dropdown-btn"
                icon="user-change"
                class="user-change-icon"
                v-tooltip.body="NLS.ChangeAssignee"
              />
              <template slot="body">
                <vu-select
                  ref="focusInput"
                  required
                  autocomplete
                  :options="adminList"
                  v-model="platformAdmin"
                  @input="assignToNewAdmin(referenceIdModal.status)"
                  class="popover-author"
                />
              </template>
            </vu-popover>
          </div>
          <div
            v-if="referenceIdModal.status === 'OPENED'"
            class="assign-to-button report-abuse-data"
          >
            <vu-popover side="bottom" :persistant="false">
              {{ NLS.AssignTo }}
              <vu-icon-btn
                class="small-icon-btn dropdown-menu-item"
                ref="dropdown-btn"
                icon="chevron-down"
              />
              <template slot="body">
                <vu-select
                  ref="focusInput"
                  required
                  autocomplete
                  :options="adminList"
                  v-model="platformAdmin"
                  @input="assignToNewAdmin('ASSIGNED')"
                  class="popover-author"
                />
              </template>
            </vu-popover>
          </div>
          <div
            v-if="
              referenceIdModal.status === 'CLOSED' ||
              referenceIdModal.status === 'REJECTED'
            "
            class="report-abuse-data"
          >
          {{
               referenceIdModal.moderator
                    ? `${referenceIdModal.moderator.first_name} ${referenceIdModal.moderator.last_name}`
                    : ""
            }}
          </div>
        </div>
        <SwymReportAbuseData
          :referenceId="referenceIdModal.subject6wuri"
          :dataFromModeration="dataForEachContent"
          :NLS="NLS"
          @getSnapshotData="getSnapshotData"
          class="report-abuse-data-table"
        />

        <SwymModerationHistory
          v-if="moderationHistoryDataModal.length > 1"
          :moderationHistoryDataModal="moderationHistoryDataModal"
          :NLS="NLS"
        />
      </template>
      <template #modal-footer>
       <div class="data-modal-footer-button">
        <div class="data-modal-button">
          <vu-btn
            :color="isCurrentUserSu ? 'primary' : 'warning'"
            class="moderation-button"
            @click="toggleModerationButton"
          >
            {{
              `${
                isCurrentUserSu
                  ? NLS.EnterModerationMode
                  : NLS.ExitmoderationMode
              }`
            }}
          </vu-btn>
        </div>
      <div class="data-modal-button data-moda-other-button">
        <div >
        <vu-btn
          :color="
            referenceIdModal.status === 'UNDER_REVIEW'
              ? 'default'
              : 'primary'
          "
          v-if="referenceIdModal.status !== 'CLOSED' && referenceIdModal.status !== 'REJECTED'"
          @click="openModerationModal(referenceIdModal.status)"
        >
          {{ NLS.Moderate }}
        </vu-btn>
       </div>
       <div >
        <vu-btn
          :color="referenceIdModal.status === 'UNDER_REVIEW' ? 'primary' : 'default'"
          v-if="
            referenceIdModal.status !== 'CLOSED' &&
            referenceIdModal.status !== 'REJECTED'
          "
          @click="openCloseModal()"
        >
          {{ NLS.CloseReport }}</vu-btn
        >
       </div>
       
       <div > 
        <vu-btn color="default" @click="toggleModal(false)">
          {{ NLS.Cancel }}
        </vu-btn>
       </div>
      </div>
       </div>
      </template>
    </vu-modal>
    <SwymReportAbuseCloseModal
      ref="modalRef1"
      @assignToAdmin="assignToAdmin"
      :NLS="NLS"
    />
    <SwymModerationButtonModal
      ref="modalRef2"
      @getModerationModeOn="getModerationModeOn"
      @assignToAdmin="assignToAdmin"
      @navigationReportAbuse="navigationReportAbuse"
      :currentUserTenantSu="currentUserTenantSu"
      :referenceIdModal="referenceIdModal"
      :NLS="NLS"
      :currentUserLogin="currentUserLogin"
          />
  </div>
</template>
<script>
import SwymReportAbuseData from "./SwymReportAbuseData.vue";
import SwymReportAbuseStatus from "./SwymReportAbuseStatus.vue";
import SwymReportAbuseCloseModal from "./SwymReportAbuseCloseModal.vue";
import SwymModerationButtonModal from "./SwymModerationButtonModal.vue";
import SwymModerationHistory from "./SwymModerationHistory.vue";
import reportAbuse from "../../mixins/reportAbuse";

export default {
  name: "SwymReportAbuseDataModal",
  components: {
    SwymReportAbuseData,
    SwymReportAbuseStatus,
    SwymReportAbuseCloseModal,
    SwymModerationButtonModal,
    SwymModerationHistory,
  },
  mixins: [reportAbuse],
  data() {
    return {
      show: false,
      disabled: false,
      platformAdmin: "",
    };
  },
  props: {
    referenceIdModal: {
      value: Object,
      default: {},
    },
    dataForEachContent: {
      value: Array,
      default: () => [],
    },
    platformAdminList: {
      value: Array,
      default: () => [],
    },
    currentUserTenantSu: {
      type: String,
      default: "",
    },
    currentUserLogin: {
      type: String,
      default: "",
    },
    NLS: {
      type: Object,
      default: () => ({}),
    },
    moderationHistoryDataModal: {
      value: String,
      default: "",
    },
  },
  methods: {
    navigationReportAbuse(data) {
      this.$emit("navigationReportAbuse", data);
    },
    toggleModal(modalVisibility) {
      this.show = modalVisibility;
    },
    openCloseModal() {
        this.$refs.modalRef1.toggleCloseModal(true);
    },
    openModerationModal() {
        this.$refs.modalRef2.toggleModeration(true);
    },
    getModerationModeOn() {
      this.$emit("getModerationModeOn");
    },
    getModerationModeExit() {
      this.$emit("getModerationModeExit");
    },
    assignToAdmin(data) {
      this.$emit("assignToAdmin", {
        login:data.login,
        updatedStatus: data.updatedStatus,
        closureType: data.closureType,
        closureReason: data.closureReason,
      });
    },
    getSnapshotData(data) {
      this.$emit("getSnapshotData", data);
    },
    contentUri(data) {
      const subjectUri = data.split(":");
      const splitItem = subjectUri.slice(-2).join(":");
      return splitItem;
    },
    assignToNewAdmin(currentStatus) {
      if (this.platformAdmin !== "") {
        this.$emit("assignToAdmin", {
          login: this.platformAdmin,
          updatedStatus: currentStatus,
        });
        this.$refs.adminChangeRef.click();
      }
    },
    toggleModerationButton() {
      if (this.isCurrentUserSu) {
        this.$emit("getModerationModeOn");
      } else {
        this.$emit("getModerationModeExit");
      }
    },
    mapIconData(iconData) {
      let icon;
      if (iconData.includes("POST")) {
        icon = "newspaper";
        return icon;
      } else if (iconData.includes("IDEATION")) {
        icon = "lamp";
        return icon;
      } else if (iconData.includes("QUESTION")) {
        icon = "comment-question";
        return icon;
      } else if (iconData.includes("WIKI_PAGE")) {
        icon = "book-open";
        return icon;
      } else if (iconData.includes("SURVEY")) {
        icon = "clipboard";
        return icon;
      } else if (iconData.includes("MEDIA")) {
        icon = "picture";
        return icon;
      } else if (iconData.includes("CONFERENCE")) {
        icon = "calendar";
        return icon;
      } else if (iconData.includes("WEDO")) {
        icon = "task";
        return icon;
      } else if (iconData.includes("COMMENT")) {
        icon = "comment";
        return icon;
      } else if (iconData.includes("ANSWER")) {
        icon = "comment";
        return icon;
      }
    },
  },
  computed: {
    adminList() {
      return this.platformAdminList.map((el, index) => {
        return {
          value: el.login,
          label: `${el.first_name} ${el.last_name}`,
          index,
        };
      });
    },
    getButtonColor() {
      currentUserTenantSu === "0" ? "primary" : "default";
    },
    isCurrentUserSu() {
      if (this.currentUserTenantSu === "0") {
        return true;
      } else {
        return false;
      }
    },
  },
};
</script>
